import Video from "../../Elements/Media/Video";
import { isTagEmbed, isTagShop } from "../../../../actions/api";
import Image from "../../Elements/Media/Image";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
const Media = ({ itemData, wallID, ownerId, wall, isCover, ThemeRule }) => {
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed || wall.User.userDatabaseType == "tagembed" ? ThemeRule.aspectImageRatio ? ThemeRule.aspectImageRatio : 100 : 100
  const mediaIcon = itemData.hotspot ? false : true
  return <div className="tb_hc_media_wrap" role="button" tabindex="0" aria-label="Pop Up Button">
    {mediaIcon ? <MediaIcon itemData={itemData} IconClass="tb_hc_media_icon" show={mediaIcon} isCenter={false} /> : null}
    {autoPlay ?
      <Video VideoClass="tb_hc_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} wall={wall} size={size} isCover={isCover} muted={true} autoPlay={autoPlay} handleVideoEnded={() => { }} />
      :
      <Image ImageClass="tb_hc_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} hotspot={true} />
    }
  </div>
}
export default Media