import React, { PureComponent, Suspense } from "react";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import CTAButton from "../../Elements/CTA/CtaButton";
import Rating from "../../Elements/Rating/Rating";
import SocialAction from "../../Elements/SocialAction/SocialAction";
import AuthorDetails from "./AuthorDetails";
import { IS_THEME, PostTrimContent, themeImpressionTracking } from '../../../../utils'
import { suiteTrack, isTagEmbed, isTagShop } from "../../../../actions/api";

const trim_content_length = !isTagEmbed ? 140 : null;
export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      isExpanded: false,
      hiddenHeight: 0,
      height: 0
    };
    this.toggleExpand = this.toggleExpand.bind(this);
    this.postTrack = React.createRef();
  }

  contentRef = ref => {
    this.setState({
      height: ref
    })

  }

  contentRefHidden = ref => {
    this.setState({
      hiddenHeight: ref
    })
  }

  toggleExpand = (event) => {
    event.stopPropagation()
    this.setState({
      isExpanded: !this.state.isExpanded
    })
  }

  componentDidMount() {
    const { itemData, itemIndex, onClickPopUpSlider } = this.props
    if (suiteTrack || isTagShop) themeImpressionTracking(this.postTrack)
    if (itemData.isPopUp) {
      onClickPopUpSlider(itemIndex, itemData)
    }
  }

  render() {
    const { itemData, personalization, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, onClickToCTA, wall, languageSetting } = this.props;
    const { isExpanded, hiddenHeight, height } = this.state
    const mediaType = itemData.type !== 1 ? true : false;
    const rating = itemData.rating && (itemData.rating > 0) ? true : false;
    const contentClass = (itemData.rating > 0) ? 'tb_hc_rating_content' : 'tb_hc_content';
    const noMediaClass = (itemData.type === 1) ? 'tb_hc_text_post' : '';
    const textDecorate = (itemData.type === 1) && String(itemData.content).length > 0 && (personalization.textDecorate) ? 'tb_hc_text_decoration' : '';
    const ctaActive = itemData.cta.status ? true : false;
    const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
    const diffrence = hiddenHeight > height

    return (
      <div item-id={itemData.id} id={`tb-hc-post-${itemData.id}`} className="tb_hc_post_wrapper" ref={this.postTrack} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} style={{ padding: personalization.padding / 2 }} role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${postTextContent}`}>
        <div className="tb_hc_post_in" onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
          <div className="tb_hc_post_media_wrapp" role="button" tabindex="0" aria-label="Pop Up Button">

            {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={'tb_hc_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /> : ''}
            {mediaType ? <Media itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} size={personalization.trimcontent ? 100 : false} isCover={personalization.trimcontent ? true : false} ThemeRule={ThemeRule} /> : null}
          </div>

          <div className={`tb_hc_contant_wrapper ${noMediaClass}`}>
            <Suspense fallback={<></>}><AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} rating={itemData.rating} instaHash={itemData.instaHash} /></Suspense>
            {ctaActive ? <div className="tb_hc_post_cta" onClick={(e) => { e.stopPropagation() }}>
              <CTAButton ctaClass={'tb_hc_post_cta_btn'} cta={personalization.cta} postCta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div>
              : ''}
            {rating ? <div className={`tb_hc_rating__`} >
              <Rating rating={itemData.rating} network={itemData.network} />
            </div> : null}
            {(isTagShop && ThemeRule.hideContent) || (!isTagShop && !ThemeRule.hideContent) || itemData.type == 1 ?
              <div className={`tb_hc_content_wrapper__`}>
                {isTagEmbed ?
                  <div className="tb_hc_content_hidden">
                    <PostContent key={itemData.id} contentClass={`${contentClass} ${textDecorate}`} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} contentRefCallBack={this.contentRefHidden} isReadMore={true} />
                  </div> : null}

                <PostContent key={itemData.id} contentClass={`${contentClass} ${textDecorate}`} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} contentRefCallBack={this.contentRef} maxLines={isExpanded ? '' : ThemeRule.lineTrim} isReadMore={true} />

                {(ThemeRule.lineTrim > 0 && diffrence) && isTagEmbed ?
                  <div className="tb_read_more_" onClick={this.toggleExpand}>
                    {/* {isExpanded ? 'Read less' : 'Read more'} */}
                    {isExpanded ? languageSetting.showLess : languageSetting.readMore}
                  </div> : null}
              </div>
              : null}

          </div>
          {isTagShop || isTagEmbed ? (ThemeRule.socialAction ? <SocialAction itemData={itemData} ThemeRule={ThemeRule} ThemeID={personalization.widgetTheme} actionClass={`tb_hc_social_action__`} /> : null) : ''}
        </div>
      </div>
    );
  }
}
