import React, { Component, memo } from "react";
import { isMobile } from 'react-device-detect';
import { connect } from "react-redux";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import Card from "./Elements/Card";
import { getDataNextSteps } from '../../../actions/themeActions'
import { isTagEmbed, isTagShop, isTagShopEditor, isTagbox } from "../../../actions/api";
import { IS_THEME, mainClass, detectMobile } from '../../../utils'
import '../scss/ThemesSplide.scss'
import './scss/HorizontalColumns.scss'
class HorizontalColumn extends Component {
  constructor(props) {
    super(props)
    this.inputRefs = {};
  }
  state = {
    windowWidth: document.getElementById(mainClass) && document.getElementById(mainClass).clientWidth ? document.getElementById(mainClass).clientWidth : window.innerWidth,
    sliderHeight: 300
  }

  componentWillMount() {
    this.onScreenLoad()
  }
  onScreenLoad = () => {
    const { renderId } = this.props.renderId
    this.setState({
      windowWidth: renderId ? document.getElementById(renderId).clientWidth : document.getElementById(mainClass) && document.getElementById(mainClass).clientWidth ? document.getElementById(mainClass).clientWidth : window.innerWidth
    })
  }

  requestData = () => {
    const { appendData, wall, preRender, hasMoreData, getDataNextSteps } = this.props;
    if (hasMoreData) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  componentDidMount() {
    var slider = document.querySelector('.tb_hc_post_slider');
    var height = slider ? slider.offsetHeight : this.state.height;
    var self = this;
    setTimeout(() => {
      this.setState({
        sliderHeight: height
      })
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element) => {
        element.removeAttribute("role");
      });
    }, 100);
    const { postData } = this.props;

    if (postData && postData.length == 1) {

      let arrow_left_id = document.querySelector(".tb_hc_arrow_left__")
      let arrow_right_id = document.querySelector(".tb_hc_arrow_right__")
      if (arrow_left_id && arrow_right_id) {
        document.querySelector(".tb_hc_arrow_left__").style = 'display:none'
        document.querySelector(".tb_hc_arrow_right__").style = 'display:none'
      }



      document.body.addEventListener('click', function () { myFunction() });

      myFunction();

      function myFunction() {
        setTimeout(() => {

          let splide01 = document.querySelector(".splide__list");

          if (splide01 != null) {
            splide01.style.transform = 'translateX(0)';
          }
        }, 100);
      }
      window.addEventListener("resize", myFunction);
    }
    else {
      window.addEventListener("resize", () => {
        this.onScreenLoad();
      });
    }

    let splide02 = document.querySelector("#kt_aside_toggle");
    if (splide02 != null) {
      splide02.addEventListener("click", this.reScrolled)
    }

    window.addEventListener('keydown', this.handleKeyPress);

  }
  handleKeyPress = event => {
    try {
      const { wall } = this.props
      if (wall.ThemeRule.slidePost == 1) {
        if (event.code === 'Space') {
          const { parentID } = this.props
          event.preventDefault(); // Prevent scrolling the page
          var Autoplay = this.inputRefs[parentID].splide.Components.Autoplay;
          if (Autoplay.isPaused()) Autoplay.play();
          else Autoplay.pause();
        }
      }
    }
    catch (error) {
    }
  }
  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleKeyPress);
  }
  reScrolled = () => {

    setTimeout(() => {
      let splide__list = document.querySelector('.splide__list');
      let getWidth = document.querySelector(".splide__slide").offsetWidth;
      const isVisibleAll = document.querySelectorAll('.splide__slide.is-visible').length;
      const isVisible = document.querySelector('.splide__slide.is-active.is-visible');
      const str = isVisible.getAttribute('aria-label').substring(0, 2)
      const getIndex = Number(str) + (isVisibleAll - 1) + isVisibleAll
      if (getIndex > 0) {
        splide__list.style.transition = '0s ease';
        splide__list.style.transform = `translateX(-${(getIndex * getWidth)}px)`;

      } else {
        splide__list.style.transform = `translateX(0px)`;
      }
    }, 200);
  }


  componentDidUpdate() {
    var slider = document.querySelector('.tb_hc_post_slider');
    var height = slider ? slider.offsetHeight : this.state.height;
    if ((height + 15) != this.state.sliderHeight) {
      setTimeout(() => {
        this.setState({
          sliderHeight: height + 15
        })
      }, 100);
    }
  }
  autoScrollSlider = (splide, prev, next, destIndex) => {
    const { wall } = this.props
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    if (autoScrollStatus == 1) {
      const currentIndex = isTagEmbed ? (splide["_o"].perPage + splide.index) : splide.index
      const slideLength = splide.length
      const permove = isTagEmbed ? 1 : splide["_o"].perPage
      if (slideLength - currentIndex === permove) this.requestData()
    }
  }

  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, hasMoreData, onClickToCTA, parentID, keyRender, languageSetting, cardNumber,onClickPopUpSlider } = this.props;

    const { sliderHeight } = this.state
    const inputName = parentID;
    const { windowWidth } = this.state;
    const numberOfPosts = wall.ThemeRule.numberOfPosts;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost && !isTagShop;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const trimContent = wall.Personalization.trimcontent === 1 ? true : false;
    const isMobileStatus = isTagEmbed ? detectMobile() : windowWidth < 768 || isMobile ? true : false

    const renderCount = numberOfPosts > parseInt(cardNumber) ? parseInt(cardNumber) : numberOfPosts
    const manageItemCount = (postData && postData.length) ? isMobileStatus ? 1 : postData.length > renderCount ? renderCount : postData.length : 3
    const desktopCount = (postData && postData.length) ? isMobileStatus ? 1 : postData.length > renderCount ? renderCount : postData.length : 4
    let typeparms = !isMobileStatus ? { type: autoScrollStatus === 1 || postData.length === 1 ? 'loop' : 'loop' } : {}
    const cloneStatus = isTagEmbed && wall.Personalization?.clonePost !== undefined ? wall.Personalization.clonePost === 0 ? { clones: 0 } : {} : {};
    const isType = autoScrollStatus != 1 ? Object.keys(cloneStatus).length > 0 ? false : 'slide' : 'slide'
    const newData = ((manageItemCount == postData.length) || (desktopCount == postData.length)) && Object.keys(cloneStatus).length == 0 ? Array(2).fill(postData).flat() : postData
    const lineTrim = wall.ThemeRule.lineTrim
    const equalHeight = !isTagEmbed && trimContent ? true : false
    const settings = {
      ...cloneStatus,
      type: autoScrollStatus === 1 ? false : isType,
      role: 'article',
      label: 'social widget',
      rewind: true,
      speed: 1000,
      loop: autoScrollStatus === 1 ? false : true,
      interval: slidePost ? slideDuration * 1000 : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : isMobileStatus ? 1 : postData.length > renderCount ? renderCount : postData.length,
      gap: 0,
      perMove: isTagEmbed ? 1 : false,
      autoplay: slidePost ? true : false,
      padding: "0",
      pagination: false,
      arrows: isMobileStatus ? true : postData.length >= (numberOfCoumn > 0 ? numberOfCoumn : desktopCount) ? true : false,
      fixedHeight: equalHeight,
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
          perMove: mobileColumn > 0 ? mobileColumn : 1,
          arrows: postData.length >= (mobileColumn > 0 ? mobileColumn : 1) ? true : false
        },
        767: {
          perPage: numberOfCoumn > 0 ? (numberOfCoumn - 3) < 1 ? numberOfCoumn : numberOfCoumn - 3 : manageItemCount,
          arrows: postData.length >= (numberOfCoumn > 0 ? (numberOfCoumn - 3) < 1 ? numberOfCoumn : numberOfCoumn - 3 : manageItemCount) ? true : false
        },
        991: {
          perPage: numberOfCoumn > 0 ? (numberOfCoumn - 4) < 1 ? numberOfCoumn : (numberOfCoumn - 4) : manageItemCount,
          arrows: postData.length >= (numberOfCoumn > 0 ? (numberOfCoumn - 4) < 1 ? numberOfCoumn : (numberOfCoumn - 4) : manageItemCount) ? true : false
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn - 1 == 0 ? numberOfCoumn : numberOfCoumn - 1 : manageItemCount,
          arrows: postData.length >= (numberOfCoumn > 0 ? numberOfCoumn - 1 == 0 ? numberOfCoumn : numberOfCoumn - 1 : manageItemCount) ? true : false
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : desktopCount,
          arrows: postData.length >= (numberOfCoumn > 0 ? numberOfCoumn : desktopCount) ? true : false
        }
      }
    };
    return <>{parentID != "" ?
      <div className="tb_hc_post_container" ref={this.sliderHeight} style={{ minHeight: sliderHeight }}>
        <Splide hasTrack={false} className={`tb_hc_post_slider ${equalHeight ? 'tb_hc_fixed_height' : ''}`} options={settings} ref={(parentID) => this.inputRefs[inputName] = parentID} tag="section" onMove={this.autoScrollSlider}>
          <SplideTrack>
            {
              ((newData && newData.length > 0)) && newData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide key={index} p_id={parentID} className={`tb_${cardData.id}`}>
                  <Card ownerId={wall.Wall.owner} itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} wall={wall} languageSetting={languageSetting} onClickPopUpSlider={onClickPopUpSlider} />
                </SplideSlide>
              })}
          </SplideTrack>
          <div className="splide__arrows splide__arrows--ltr tb_hc_arrow_wrapper_" role="navigation" aria-label="Slider Navigation Wrapper">
            <div role="navigation" className="tb_hc_arrow splide__arrow splide__arrow--prev tb_hc_arrow_left__ tb__icon tb-arrow-left-alt" aria-labelledby="Navigation Previous Slide"> </div>
            <div role="navigation" className="tb_hc_arrow splide__arrow splide__arrow--next tb_hc_arrow_right__ tb__icon tb-arrow-right-alt" aria-labelledby="Navigation Next Slide" onClick={isTagShop ? this.requestData : null}> </div>
          </div>
        </Splide>
      </div> : null}</>
  }
}




const mapStateToProps = state => {
  return {

  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default memo(connect(mapStateToProps, mapDispatchToProps)(HorizontalColumn));